<template>
  <div id="">
    <div class="pcontent">
        <div class="back" @click="back()">返回</div>
        <div class="f-bgf f-mb10">
            <img :src="api+list.img_url" class="f-w1" alt="">
            <div class="f-p5">
                <p class="f-fs14 f-fwb">{{list.title}}</p>
                <p class="f-cr">{{list.price}}/份</p>
            </div>
        </div>
        <div class="f-bgf f-p10">
            <p class="f-fs12 f-fwb f-mb5">商品详情</p>
            <div v-html="list.content"></div>
        </div>

        <div class="bottom f-cb">
            <div class="f-fl">
                <div class="f-fl f-lh3 f-fs12">
                    数量：
                </div>
                <div class="u-numinput f-ib">
                    <div class="jian" @click="decNum()">-</div>
                    <input type="text" v-model="num">
                    <div class="jia" @click="++num">+</div>
                </div>
            </div>
            <div class="f-fr">
                <div class="u-btn1 f-fs12" @click="addCart()">加入购物车</div>
            </div>
        </div>
    </div>
  </div>
</template>

<script>
import Config from '../model/config.js';
export default {
  name: 'app',
  data () {
    return {
      api:Config.api,
      list:'',
      num:1,
      uid:Config.storage.get('uid')
    }
  },methods:{
    load(){
      var id=this.$route.query.id;
      var api=this.api+"api/productcontent?id="+id;
      this.$http.get(api).then((response)=>{
        console.log(response);
        this.list=response.body.result[0];
        this.num=this.list.num;
      })
    },
    decNum(){
      if(this.num>1){
        --this.num;
      }
    },
    back(){
      this.$router.push('/home');
    },
    addCart(){
      var api=this.api+'api/addcart';
      this.$http.post(api,{
        uid:this.uid,
        title:this.list.title,
        product_id:this.list.cate_id,
        img_url:this.list.img_url,
        price:this.list.price,
        num:this.num
      }).then((response)=>{
        if(response){
          this.$socket.emit('addcart', 'addcart'); //加入购物车时给服务器发消息
          this.$router.push('/home');
        }
      },(err)=>{
        console.log(err);
      })
    }
  },mounted(){
    this.load();
  }
}
</script>

<style lang="scss">
.pcontent{
    padding-bottom:5rem;
    .bottom{position: fixed;left:0;bottom:0;width:100%;height:5rem;padding:1rem .5rem;border-top:1px solid #ccc;text-align:center;background:#fff;box-sizing:border-box;
        
    }
    .back{position: fixed;top:.5rem;left:.5rem;width:4rem;height:4rem;line-height:4rem;font-size:1.2rem;color:#fff;text-indent:1.2rem;background:rgba(0, 0, 0, .6);border-radius:50%;
        &:before{content:'';display:block;position:absolute;top:1.5rem;left:.4rem;width:.8rem;height:.8rem;border-left:.2rem solid #fff;border-bottom:.2rem solid #fff;transform:rotate(45deg);}
    }
}
</style>



