<template>
  <div id="">
    <div class="index">
			<div class="header">
				<ul>
					<li>
						<router-link to='/hot'><img src="../assets/img/rexiao.png" ><br>热销榜</router-link>
					</li>
					<li>
						<img src="../assets/img/caidan.png" ><br>点过的菜
					</li>
					<li>
						<router-link to='/search'><img src="../assets/img/sousuo.png" ><br>搜你喜欢</router-link>
					</li>
				</ul>
			</div>

      <div v-for="item in list">
        <div class="list_title">{{item.title}}</div>
        <div class="list">
          <ul>
            <li v-for="i in item.list">
              <div class="inner">
                <router-link :to="'/pcontent?id='+i._id">
                <img :src="api+i.img_url" />
                <p class="title">{{i.title}}</p>						
                <p class="price">¥{{i.price}}</p>
                </router-link>
              </div>
            </li>
          </ul>
        </div>
      </div>
			
		</div>

		<div class="side_cate" id="side_cate">
			<ul>
				<li v-for="(item,key) in list" @click="changeList(key)">{{item.title}}</li>
			</ul>
			<div class="nav_cate" id="nav_cate">
				<img src="../assets/img/nav.png"/>
					<p>菜单</p>
			</div>
		</div>
		<div class="bg" id="bg"></div>

		<footer_nav></footer_nav>
    <footer_cart></footer_cart>
  </div>
</template>

<script>
import Footer_nav from './public/Footer_nav.vue';
import Footer_cart from './public/Footer_cart.vue';
import Config from '../model/config.js';
export default {
  name: 'app',
  data () {
    return {
      api:Config.api,
      list:[]
    }
	},methods:{
    loadData(){
      var api=this.api+'api/productlist';
      this.$http.get(api).then((response)=>{
        // console.log(response);
        this.list=response.body.result;
      })
    },
    //菜单定位
    changeList(key){
      var itemCatesDom = document.querySelectorAll(".list_title");
      document.documentElement.scrollTop=itemCatesDom[key].offsetTop;
      var bg=document.getElementById('bg');
      bg.click();
    }
  },mounted(){
    this.loadData();
    var side_cate=document.getElementById('side_cate');
    var nav_cate=document.getElementById('nav_cate');
    var bg=document.getElementById('bg');
    var state=true;
    nav_cate.onclick=bg.onclick=function(){
      if(state){
        state=false;
        side_cate.style.transform='translate(0,0)';
        bg.style.display='block';
      }else{
        state=true;
        side_cate.style.transform='translate(-100%,0)';
        bg.style.display='none';
      }
    }
  },components:{
    'footer_nav':Footer_nav,
    'footer_cart':Footer_cart
  }
}
</script>

<style lang="scss">
.index{
	padding:.5rem;
	.header ul{
		display:flex;
		flex-wrap: wrap;
		margin-bottom:1rem;
		border-radius: .5rem;
		background:#fff;
		li{
			flex: 1;
			padding:0.5rem 0;
			border-right:1px solid #f1f1f1;
			text-align:center;
			img{
				width:1.6rem;
			}
			&:last-child{
				border-right:none;
			}
		}
	}
	.list_title{
		font-size:1.2rem;
		font-weight:bold;
		text-align:center;
		line-height:1.8rem;
	}
	.list ul{
		display:flex;
		flex-wrap: wrap;
		margin-bottom:.5rem;
		li{
			width:33.32%;
			padding:.5rem;
			box-sizing: border-box;
			.inner{
				border-radius: .5rem;
				overflow: hidden;
				background:#fff;
				p{padding-left:.5rem;}
				.title{
					font-weight: bold;
				}
				.price{
					font-size:0.8rem;
				}
				img{
					width:100%;color:#fff;
				}
			}
			
		}
	}
}

.side_cate{
	position: fixed;
	top:0;
	left:0;
	width:6rem;
	height:100%;
	padding-left:.5rem;
	background: #e7e7e7;
	transition: all .5s;
	transform: translate(-100%,0);
	z-index:3;
	li{
		line-height:3rem;
	}
	.nav_cate{
		position: absolute;
		top:45%;
		left:6.5rem;
		width:3rem;
		height:4rem;
		padding-right:.5rem;
		text-align:center;
		border-radius: 0 50% 50% 0;
		background: rgba(132, 128, 128, 0.9);
		
		img{
			width:2rem;
			margin-top:.5rem;
		}
		p{
			margin-top:-.5rem;
			color:#fff;
		}
	}
}

.bg{
	display: none;
	position: fixed;
	top:0;
	left:0;
	width:100%;
	height:100%;
	background: rgba(0, 0, 0, 0.5);
}
</style>



