<template>
  <div id="">
    <div class="start_content">
			<div class="start_head">
				<img src="../assets/img/canju.png"/> 修改用餐人数
			</div>
			<div class="f-tac f-cr text">请选择正确的用餐人数 ，小二马上给你送餐具</div>
			<div class="content f-mb30">
				<ul>
					<li v-for="(item,key) in userList"><span :class="{'active':parseInt(peopleinfo.p_num)==key+1}">{{item}}</span></li>
				</ul>
			</div>

			<input type="text" class="f-wb9 f-db f-lh2 f-mlra f-mb10" v-model="mark" placeholder="备注信息">
      <ul class="mark">
        <li><span>不辣</span></li>
        <li><span>微辣</span></li>
        <li><span>中辣</span></li>
        <li><span>重辣</span></li>
      </ul>

      <div class="footer_nav" @click="$router.go(-1)">
        <img src="../assets/img/close.png" alt="">
        <p>返回</p>
      </div>
      <div class="footer_cart" @click="save()">
        <img src="../assets/img/doorder.png" alt="">
        <p>保存</p>
      </div>
			
		</div>
  </div>
</template>

<script>
import Config from '../model/config.js';
export default {
  name: 'app',
  data () {
    return {
      num:'1人',
      mark:'',
      api:Config.api,
      uid:Config.storage.get('uid'),
      peopleinfo:'',
      userList:[]
    }
  },methods:{
    peopleInfoList(){
      for(var i=1;i<13;i++){
        this.userList.push(i+'人');
      }
      var api=this.api+'api/peopleInfoList?uid='+this.uid;
      this.$http.get(api).then((response)=>{
        this.peopleinfo=response.body.result[0];
        this.num=this.peopleinfo.p_num;
        this.mark=this.peopleinfo.p_mark;
      },(err)=>{
        console.log(err);
      })
    },
    choose(){
      //选择人数
      var that=this;
      var arrs = document.querySelectorAll('.content span');
      for(var i=0;i<arrs.length;i++){
        arrs[i].onclick=function(){
          for(var j=0;j<arrs.length;j++){
            arrs[j].className='';
          }
          this.className='active';
          that.num=this.innerHTML.trim();//trim()去空格          
        }
      }
      var arrs1 = document.querySelectorAll('.mark span');
      for(var i=0;i<arrs1.length;i++){
        arrs1[i].onclick=function(){
          for(var j=0;j<arrs1.length;j++){
            arrs1[j].className='';
          }
          this.className='active';
          that.mark=this.innerHTML.trim();//trim()去空格          
        }
      }
    },save(){
      var api=this.api+'api/addPeopleInfo';
      this.$http.post(api,{
        uid:this.uid,
        p_num:this.num,
        p_mark:this.mark
      }).then((response)=>{
        if(response.body.success){
          this.$socket.emit('addcart', 'addcart'); //给服务器发消息
          this.$router.push('/cart');
        }
      },(err)=>{
        console.log(err);
      })
    }
  },mounted(){
    this.peopleInfoList();
    // 数据渲染完成再去获取dom节点
    this.$nextTick(function(){
      this.choose();
    })
    
  }
}
</script>

<style lang="scss">
.start_content{
	padding-top:5rem;
	.start_head{
		width:15rem;margin:0 auto 1rem;;height:3rem;line-height:3rem;font-size:1.4rem;color:#fff;text-align: center;background:#000;
		img{height:2rem;vertical-align: middle;}
	}
	.text{margin-bottom:2rem;}
	.content ul,.mark{
		display:flex;flex-wrap: wrap;padding:0 0.5rem;
		li{width:25%;padding:0.5rem;box-sizing: border-box;
			span{display: block;line-height:3rem;text-align:center;border:1px solid #999;background-color:#fff;border-radius: 0.5rem;}
    }
    .active{color:#fff;border:none;background-color: red;}
	}
	.start{position: fixed;bottom:5rem;left:50%;width:6rem;height:5rem;padding-top:1rem;margin-left:-3rem;font-size:1.4rem;text-align:center;color:#fff;background:red;border-radius: 50%;}
}
</style>



