import Vue from 'vue';
import App from './App.vue';
import Router from 'vue-router';
Vue.use(Router);
import Resource from 'vue-resource';
Vue.use(Resource);
import Config from './model/config.js';
import VueSocketIO from 'vue-socket.io';
Vue.use(new VueSocketIO({
  connection: 'http://a.itying.com?roomid='+Config.storage.get('uid')
}))

import Start from './components/Start.vue';
import Home from './components/Home.vue';
import Pcontent from './components/Pcontent.vue';
import Hot from './components/Hot.vue';
import Search from './components/Search.vue';
import Cart from './components/Cart.vue';
import Order1 from './components/Order1.vue';
import Order2 from './components/Order2.vue';
import Editinfo from './components/EditInfo.vue';
import Success from './components/Success.vue';

const routes=[
  {path:'/start',component:Start},
  {path:'/home',component:Home},
  {path:'/pcontent',component:Pcontent},
  {path:'/hot',component:Hot},
  {path:'/search',component:Search},
  {path:'/cart',component:Cart},
  {path:'/order1',component:Order1},
  {path:'/order2',component:Order2},
  {path:'/editinfo',component:Editinfo},
  {path:'/success',component:Success},
  {path:'*',redirect:'/start'},
]

const router = new Router({
  routes
})

new Vue({
  el: '#app',
  router,
  // sockets: {
  //   connect: function () {
  //       console.log('连接成功')
  //   }
  // },
  render: h => h(App)
})



