<template>
  <div id="">
    <div class="f-p10">
        <div class="f-p10 f-bgf f-bdr5 f-mb10">
            <div class="f-cb">
                <img src="../assets/img/timer.png" class="f-fl f-w50 f-mr5" alt="">
                <p class="f-fs16 f-fwb f-pt5 f-mb5">{{uid}}号桌待门店接单</p>
                <p>请及时联系服务员确认点单信息！</p>
            </div>
            <div class="f-fs14 f-fwb">已点菜品{{list.total_num}}份，合计：<span class="f-fs20 f-cr">{{list.total_price}}元</span></div>
        </div>
        <div class="f-bgf f-bdr5">
            <div class="f-p5 f-fs12 f-fwb f-bb1">菜品详情：</div>
            <ul class="f-p10 f-lh2">
                <li class="f-cb" v-for="item in list.items">
                    <div class="f-wb6 f-fl">{{item.title}}</div>
                    <div class="f-wb2 f-fl">{{item.num}}份</div>
                    <div>{{item.price}}元</div>
                </li>
            </ul>
        </div>
    </div>

    <footer_nav></footer_nav>
    <div class="footer_cart" @click="$router.push('/order2')">
        <img src="../assets/img/doorder.png" alt="">
        <p>结账</p>
    </div>
  </div>
</template>

<script>
import Footer_nav from './public/Footer_nav.vue';
import Config from '../model/config.js';
export default {
  name: 'app',
  data () {
    return {
      api:Config.api,
      uid:Config.storage.get('uid'),
      list:''
    }
  },methods:{
      getOrder(){
        var api=this.api+'api/getOrder?uid='+this.uid;
        this.$http.get(api).then((response)=>{
            console.log(response);
            this.list=response.body.result[0];
        })
      }
  },mounted(){
      this.getOrder();
  }
  ,components:{
    'footer_nav':Footer_nav
  }
}
</script>

<style lang="scss">

</style>



