<template>
  <div id="">
    <div class="f-p10">
        <div class="f-bgf f-bdr5 f-mb10">
            <div class="f-p10 f-bb1">
                <div class="f-fs16 f-fwb f-tac f-ptb10 f-mb5">已下单的菜</div>
                <ul class="f-cb">
                    <div class="f-wb3 f-fl">桌号：{{uid}}</div>
                    <div class="f-wb3 f-fl">人数：{{list.p_num}}</div>
                    <div>下单时间：{{time}}</div>
                </ul>
            </div>
            <div class="f-ptb10 f-plr5 f-lh3">
                应付金额：<span class="f-fs16 f-fwb f-cr">￥{{list.total_price}}</span>
            </div>
        </div>
        <div class="f-p20 f-bgf f-bdr5 f-mb10">
            <span class="u-btn f-mr10" @click="doPay()">支付宝支付</span>
            <span class="u-btn u-btn-c3" @click="doWeixinPay()">微信支付</span>
        </div>
        <div class="f-bgf f-bdr5">
            <div class="f-p5 f-fs12 f-fwb f-bb1">菜品详情：</div>
            <ul class="f-p10 f-lh2">
                <li class="f-cb" v-for="item in list.items">
                    <div class="f-wb6 f-fl">{{item.title}}</div>
                    <div class="f-wb2 f-fl">{{item.num}}份</div>
                    <div>{{item.price}}元</div>
                </li>
            </ul>
        </div>
    </div>

    <footer_nav></footer_nav>
  </div>
</template>

<script>
import Footer_nav from './public/Footer_nav.vue';
import Config from '../model/config.js';
export default {
  name: 'app',
  data () {
    return {
      api:Config.api,
      uid:Config.storage.get('uid'),
      list:'',
      time:''
    }
  },methods:{
      getOrder(){
        var api=this.api+'api/getOrder?uid='+this.uid;
        this.$http.get(api).then((response)=>{
            console.log(response);
            this.list=response.body.result[0];
            var time=new Date(this.list.add_time);
            console.log(time);
            var hours=time.getHours();
            var minutes=time.getMinutes();
            if(hours<=9){
                hours="0"+hours;
            }
            if(minutes<=9){
                minutes="0"+minutes;
            }
            this.time=hours+":"+minutes;
        })
      },
      doPay(){
        var that=this;
        var api=this.api+'api/doPay';
        this.$http.post(api,{
            uid:this.uid,                      
            total_price:that.list.total_price,
            order_id:that.list.order_id,
            return_url:'http://localhost:8080/#/success'                         
        }).then((response)=>{                              
            console.log(response);                        
            location.href=response.body.result.data;
        },(err)=>{
            console.log(err);
        })
      },
      doWeixinPay(){                                      
        var order_id=this.list.order_id;
        location.href='https://open.weixin.qq.com/connect/oauth2/authorize?appid=wx7bf3787c783116e4&redirect_uri=http://b.itying.com?order_id='+order_id+'&response_type=code&scope=snsapi_base#wechat_redirect';
      }
  },mounted(){
      this.getOrder();
  },components:{
    'footer_nav':Footer_nav
  }
}
</script>

<style lang="scss">

</style>



