<template>
  <div id="">
    <div class="f-p10">
      <div v-if="totalNum">
        <div class="f-p5 f-bgf f-mb10 f-bdr5">
            <div class="f-fs16 f-fwb f-bb1 f-ptb5 f-tac">购物车</div>
            <div class="f-ptb10 f-bb1">
                <div class="f-fr f-lh1">
                  <router-link to="/editinfo">
                    <img src="../assets/img/edit.png" class="f-w20" alt="">
                    <p>修改</p>
                  </router-link>
                </div>
                <p class="f-cr">用餐人数：{{peopleinfo.p_num}}</p>
                <p>备注：{{peopleinfo.p_mark}}</p>
            </div>
            <div class="f-pt10 f-bb1">
                <p>购物车中共有{{totalNum}}个菜</p>
                <p>合计：<span class="f-fs20 f-cr">￥{{allPrice}}</span></p>
            </div>
        </div>
        <ul class="f-p5 f-bgf f-bdr5 f-mb10">
            <li class="f-ptb10 f-bb1 f-cb" v-for="(item,key) in list">
                <div class="u-numinput f-fr">
                    <div class="jian" @click="decNum(item,key)">-</div>
                    <input type="text" v-model="item.num" @change="getTotalResult();">
                    <div class="jia" @click="incNum(item)">+</div>
                </div>
                <img :src="api+item.img_url" class="f-fl f-w40 f-mr5 f-bdr5" alt="">
                <p class="f-fs12 f-fwb">{{item.title}}</p>
                <p>￥{{item.price}}</p>
            </li>
        </ul>
      </div>

        <div class="f-bgf f-p5 f-bdr5">
            <div class="f-fs12 f-fwb f-mb5">本店顾客最长点的菜</div>
            <div class="m-scrool1">
                <ul>
                    <li class="f-w80 f-mr5">
                        <img src="../assets/img/1.jpg" class="f-w1 f-bdr5" alt="">
                        <p>大蒜腊肉</p>
                        <p>￥26</p>
                    </li>
                </ul>
            </div>
        </div>
      <div v-if="!totalNum" class="f-pt50 f-tac">您的购物车空空的，点击菜单开始点菜</div>
    </div>

    <footer_nav></footer_nav>
    <div class="footer_book" id="footer_nav" @click="$router.push('/home')">
        <img src="../assets/img/book.png" alt="">
        <p>菜单</p>
    </div>
    <div class="footer_cart" @click="addOrder()">
        <img src="../assets/img/doorder.png" alt="">
        <p>下单</p>
    </div>
  </div>
</template>

<script>
import Footer_nav from './public/Footer_nav.vue';
import Config from '../model/config.js';
export default {
  name: 'app',
  data () {
    return {
      api:Config.api,
      list:'',
      uid:Config.storage.get('uid'),
      peopleinfo:'',
      allPrice:'',
      totalNum:''
    }
  },sockets: {
        addcart: function (data) {  //接收服务器传过来的addcart
            this.loadData();
            this.peopleInfoList();
        }
  },methods:{
    loadData(){
      var api=this.api+'api/cartlist?uid='+this.uid;
      this.$http.get(api).then((response)=>{
        this.list=response.body.result;
        this.getTotalResult();
      },(err)=>{
        console.log(err);
      })
    },
    decNum(item,key){
      var api=this.api+'api/decCart?uid='+this.uid+'&product_id='+item.product_id+'&num='+item.num;
      this.$http.get(api).then((response)=>{
        console.log(response);
        this.getTotalResult();
      },(err)=>{
        console.log(err);
      })
      if(item.num==1){
        this.list.splice(key,1);
      }else{
        this.$socket.emit('addcart', 'addcart'); //加入购物车时给服务器发消息
        --item.num;
      }
    },incNum(item){
      var api=this.api+'api/incCart?uid='+this.uid+'&product_id='+item.product_id+'&num='+item.num;
      this.$http.get(api).then((response)=>{
        console.log(response);
        this.getTotalResult();
      },(err)=>{
        console.log(err);
      })
      this.$socket.emit('addcart', 'addcart'); //加入购物车时给服务器发消息
      ++item.num;
    },peopleInfoList(){
      var api=this.api+'api/peopleInfoList?uid='+this.uid;
      this.$http.get(api).then((response)=>{
        this.peopleinfo=response.body.result[0];
      },(err)=>{
        console.log(err);
      })
    },getTotalResult(){
      var allPrice=0,
          totalNum=0;
      for(var i=0;i<this.list.length;i++){
        allPrice+=parseFloat(this.list[i].price*this.list[i].num);
        totalNum+=this.list[i].num;
      }
      this.allPrice=parseFloat(allPrice);
      this.totalNum=parseFloat(totalNum);
    },addOrder(){ //提交订单
      var api=this.api+'api/addOrder';
      this.$http.post(api,{
        uid:this.uid,
        p_num:this.peopleinfo.p_num,
        p_mark:this.peopleinfo.p_mark,
        order:JSON.stringify(this.list),
        total_price:this.allPrice,
        total_num:this.totalNum
      }).then((response)=>{
        // console.log(response);
        if(response.body.success){
          this.$socket.emit('addcart', 'addcart'); //给服务器发消息
          this.$router.push('/order1');
        }
      },(err)=>{
        console.log(err);
      })
    }
  },mounted(){
    this.loadData();
    this.peopleInfoList();
    
  },components:{
    'footer_nav':Footer_nav
  }
}
</script>

<style lang="scss">

</style>



