<template>
<div>
    <router-link to='/cart'>
        <div class="footer_cart">
            <img src="../..//assets/img/cart.png" alt="">
            <p>购物车</p>
            <div class="tip" v-if="data.result">{{data.result}}</div>
        </div>
    </router-link>
</div>
</template>

<script>
import Config from '../../model/config.js';
export default {
    data(){
        return{
            api:Config.api,
            uid:Config.storage.get('uid'),
            data:''
        }
    },sockets: {
        addcart: function () {  //接收服务器传过来的addcart
            this.load();
        }
    },methods:{
        load(){
            var api=this.api+"api/cartCount?uid="+this.uid;
            this.$http.get(api).then((response)=>{
                // console.log(response);
                this.data=response.body;
            })
        }
    },mounted(){
        this.load();
    }
}
</script>


<style lang="scss">

</style>



