<template>
    <div>
        <div class="footer_nav" id="footer_nav" @click="flag=!flag" v-if="!flag">
			<img src="../../assets/img/navigation.png" alt="">
			<p>导航</p>
		</div>
		<div class="footer_nav_show" id="footer_nav_show" v-if="flag">
			<ul>
				<li>
					<router-link to="/home">
						<img src="../../assets/img/book.png" alt="">
						<p>菜单</p>
					</router-link>
				</li>
				<li>
					<router-link to="/cart">
						<img src="../../assets/img/cart.png" alt="">
						<p>购物车</p>
					</router-link>
				</li>
				<li>
					<router-link to="/order1">
						<img src="../../assets/img/order.png" alt="">
						<p>我的订单</p>
					</router-link>
				</li>
				<li>
					<router-link to="/order2">
						<img src="../../assets/img/wallet.png" alt="">
						<p>结账</p>
					</router-link>
				</li>
				<li id="back" @click="flag=!flag">
					<img src="../../assets/img/close.png" alt="">
					<p>关闭</p>
				</li>
			</ul>
		</div>
    </div>
</template>

<script>
export default {
    data(){
        return{
            flag:false
        }
    }
}
</script>


<style lang="scss">

</style>



